
import XHeader from 'vux/src/components/x-header/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'

export default {
data() {
return {
noData: false,
pageIndex: 1,
pageSize: 5,
list: [],
};
},
mounted() {

this.$refs.scroll.$el.style.height = document.body.clientHeight-124+'px';
this.getHighRisk(true);
},
methods: {

srcValue(TYPE) {
if (TYPE == 1) {
return require("../../imgs/HealthRecords/small_house_pink.png");
} else if (TYPE == 2) {
return require("../../imgs/HealthRecords/small_doctor_pink.png");
}
},

getHighRisk(upOrDown) {
if (upOrDown) {
this.pageNumber = 1;
}
this.$vux.loading.show({
text: '加载中...'
});
this.$ajax.post('',{
"MSH.1": "gaoweiService",
"MSH.2": "list",
'GRDAID':this.$root.loginInfo.grdaid,
"CHZBM": this.$root.loginInfo.CHZBM,
"pageNumber": this.pageIndex,
"pageSize": this.pageSize,
'token':this.$root.loginInfo.token,
}).then(res => {
console.log(res);
this.$vux.loading.hide();
if(typeof res != 'undefined' && typeof res.rows != 'undefined') {
if (res.pageNumber == 1) {
this.pageIndex = 1,
this.list = [];
}
if (res.rows.length > 0) {
this.noData = false;
this.pageIndex++;
this.list = [...this.list,...res.rows];
} else {
if (res.pageNumber > 1) {
this.$vux.toast.text('没有更多了', 'bottom');
} else {
this.noData = true;
}
}
}
this.$refs.scroll.finishPullUp();
this.$refs.scroll.finishPullDown();
}).catch(error => {
console.log(error);
this.$vux.loading.hide();
this.$refs.scroll.finishPullUp();
this.$refs.scroll.finishPullDown();
})
},

gotoDetail(TYPE, CBH, N) {
if (TYPE == "DJ") {
this.$router.push({name: "descHighRiskReg", params: {N: N, CBH: CBH}});
} else if (TYPE == "PF") {
this.$router.push({name: "descHighRiskScore", params: {N: N, CBH: CBH}});
}
},
},
components: {
XHeader
}
};



